/* fonts start */
/* playfair-display-regular - latin_cyrillic */
@font-face {
  font-family: 'Playfair Display';
  font-style: normal;
  font-weight: 400;
  src: url('./PlayfairDisplay/playfair-display-v30-latin_cyrillic-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('./PlayfairDisplay/playfair-display-v30-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('./PlayfairDisplay/playfair-display-v30-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('./PlayfairDisplay/playfair-display-v30-latin_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
       url('./PlayfairDisplay/playfair-display-v30-latin_cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('./PlayfairDisplay/playfair-display-v30-latin_cyrillic-regular.svg#PlayfairDisplay') format('svg'); /* Legacy iOS */
}
